import { sentence } from "./data";

class App {
  constructor() {
    // HTML Element
    this.textBox = document.getElementById("textBox");
    this.canvas = document.getElementById("canvas");
    this.ctx = this.canvas.getContext("2d");
    // Settings
    this.pixelRatio = window.devicePixelRatio > 1 ? 2 : 1;
    // LifeCycle
    this.init();
    this.start();
  }

  init() {
    this.resize();
    this.addEventListener();
  }

  start() {
    this.draw();
    window.requestAnimationFrame(this.animate.bind(this));
  }

  animate(t) {
    this.ctx.clearRect(0, 0, this.stageWidth, this.stageHeight);

    this.textBox.style.clipPath = `circle(100px at 100px 100px)`;
    this.ctx.strokeRect(450, 150, 1000, 200);
    const imageData = this.ctx.getImageData(
      0,
      0,
      this.stageWidth,
      this.stageHeight
    );
    // console.log(imageData.data);
    window.requestAnimationFrame(this.animate.bind(this));
  }

  draw() {
    const div = document.createElement("div");
    div.classList.add("sentence");
    div.innerText = "우리는 저마다 다른 인생을 꿈꾸어야 한다";
    this.textBox.appendChild(div);
  }

  addEventListener() {
    window.addEventListener("resize", this.resize.bind(this), false);
  }

  resize() {
    this.stageWidth = document.body.clientWidth;
    this.stageHeight = document.body.clientHeight / 2;

    this.canvas.width = this.stageWidth * this.pixelRatio;
    this.canvas.height = this.stageHeight * this.pixelRatio;
    this.ctx.scale(this.pixelRatio, this.pixelRatio);
    const src = this.ctx.getImageData(0, 0, 100, 100);
    const pixels = src.data;
    console.log(pixels);
    console.log(pixels.length);
  }
}

window.onload = () => {
  const app = new App();
};
